@tailwind base;
@tailwind components;
@tailwind utilities;
@layer base {
  body {
    font-family: Inter;
  }
  .card-data::-webkit-scrollbar {
    display: none;
  }
  .card-data {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none;
  }
  .flip-card {
    background-color: transparent;
    width: 100%;
    perspective: 1000px;
  }

  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }

  .flip-card:hover .flip-card-inner {
    /* transform: rotateY(180deg); */
  }

  .flip-card-front,
  .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }

  .flip-card-front {
    background-color: #bbb;
    color: black;
  }

  .flip-card-back {
    background-color: #2980b9;
    color: white;
    transform: rotateY(180deg);
  }
  .slider-parent {
    position: relative;
  }
  .buble {
    position: relative;
  }
}
